// --positionXOpposite: -20px;
// --positionY: 20px;
// --positionYOpposite: -20px;
// --angle: 145deg;
// --blur: 60px;
// --textColor: #001f3f;
// --textColorOpposite: #e0e0e0;
// --baseColor: #e0e0e0;
// --darkColor: #bebebe;
// --lightColor: #ffffff;
// --firstGradientColor: #e0e0e0;
// --secondGradientColor: #e0e0e0;
// --size: 300px;
// --radius: 50px

function NeumorphismSimpleButton(props: ButtonProps) {
    const { onPress, size = 150, distance = 20, blur = 35, borderRadius = 30 } = props;
    const blurAnimation = useRef(new Animated.Value(0)).current;
    const shadowColorOneAnimation = useRef(new Animated.Value(0)).current;
    const shadowColorTwoAnimation = useRef(new Animated.Value(0)).current;

    const shadowOneAnimation = shadowColorOneAnimation.interpolate({
        inputRange: [0, 1],
        outputRange: ['#bebebe', '#ffffff']
    })

    const shadowTwoAnimation = shadowColorTwoAnimation.interpolate({
        inputRange: [0, 1],
        outputRange: ['#fff', '#bebebe']
    })
    const [isDown, setDown] = useState(false);




    const animateIn = () => {
        // Will change fadeAnim value to 1 in 5 seconds



        Animated.timing(shadowColorTwoAnimation, {
            toValue: 1,
            duration: 5000,
            useNativeDriver: false
        }).start(() => {
            Animated.timing(shadowColorOneAnimation, {
                toValue: 1,
                duration: 5000,
                useNativeDriver: false
            }).start();
        });


    };


    const animateOut = () => {



        Animated.timing(shadowColorTwoAnimation, {
            toValue: 0,
            duration: 5000,
            useNativeDriver: false
        }).start(() => {
            Animated.timing(shadowColorOneAnimation, {
                toValue: 0,
                duration: 5000,
                useNativeDriver: false
            }).start();
        });


    };


    const handleOnPress = () => {
        if (isDown) {
            setDown(false);
            animateOut();
        } else {
            setDown(true);

            animateIn();
        }
    }

    const handlePressIn = () => {
        animateIn()
        setDown(true);
    }

    const handlePressOut = () => {
        animateOut()
        setDown(false);
    }

    const gradientColors = useMemo(() => isDown ? ['#d1d9e6', '#ffffff'] : ['#ffffff', '#d1d9e6'], [isDown]);
    const gradientStart = useMemo(() => isDown ? ({ x: 145, y: 125 }) : ({ x: 145, y: 125 }), [isDown]);
    const gradientEnd = useMemo(() => isDown ? ({ x: 145, y: 145 }) : ({ x: 1, y: 145 }), [isDown]);

    const shadowColors = useMemo(() => isDown ? ['#bebebe', '#fff'] : ['#fff', '#bebebe'], [isDown]);

    console.log(shadowOneAnimation)
    return (
        <TouchableWithoutFeedback
            onPress={handleOnPress}
        >
            {/* <View style={{ width: size + distance, height: size + distance, borderWidth: 1, justifyContent: 'center', alignItems: 'center' }}> */}

            <ButtonOuter style={[{

                shadowColor: shadowOneAnimation,
            }]} borderRadius={borderRadius} blur={blur} distance={distance} size={size}>
                <ButtonInner style={[{

                    shadowColor: shadowTwoAnimation,
                }]} borderRadius={borderRadius} blur={blur} distance={distance} size={size}>
                    {/* <ButtonFaceGradient
                        borderRadius={borderRadius} distance={distance} size={size} colors={gradientColors}  >
                    </ButtonFaceGradient> */}
                </ButtonInner>
            </ButtonOuter>
            {/* </View> */}

        </TouchableWithoutFeedback>
    );
}

NeumorphismSimpleButton.defaultProps = {

}

export default NeumorphismSimpleButton;
